import { useState } from "react";
import { Row, Col } from "antd";
import { withTranslation } from "react-i18next";
import { SvgIcon } from "../../common/SvgIcon";
import Container from "../../common/Container";

import i18n from "i18next";
import {
  FooterSection,
  Title,
  Extra,
  Para,
  Large,
  FooterContainer,
  LogoContainer,
  Language,
  Label,
  LanguageSwitch,
  LanguageSwitchContainer,
  SocialIcon,
} from "./styles";

interface SocialLinkProps {
  href: string;
  src: string;
}

const Footer = ({ t }: any) => {
  const handleChange = (language: string) => {
    i18n.changeLanguage(language);
  };

  const SocialLink = ({ href, src }: SocialLinkProps) => {
    return (
      <SocialIcon
        href={href}
        target="_blank"
        rel="noopener noreferrer"
        key={src}
        aria-label={src}
      >
        <SvgIcon src={src} width="50px" height="50px" />
      </SocialIcon>
    );
  };

  const [visible, setVisibility] = useState(false);
  const scrollTo = (id: string) => {
    const element = document.getElementById(id) as HTMLDivElement;
    element.scrollIntoView({
      behavior: "smooth",
    });
    setVisibility(false);
  };

  return (
    <>
      <FooterSection>
        <Container> 
          <Row justify="center">
            <LogoContainer aria-label="homepage">
              <SvgIcon src="logo.svg" width="200px" height="64px" />
            </LogoContainer>
          </Row>
          <Row justify="space-between"
               align="middle">
                 
            <FooterContainer>
              <SocialLink
                href="https://www.youtube.com/channel/UCeOAc6gVvHAXgJfxUhMlXww"
                src="youtube.svg"
              />
              <SocialLink
                href="https://twitter.com/TeamRoblian"
                src="twitter.svg"
              />
              <SocialLink
                href="https://www.roblox.com/groups/9757404/Team-Roblian#!/about"
                src="roblox.svg"
              />
            </FooterContainer>
          </Row>
          <Row justify="space-between">
            <Col lg={10} md={10} sm={12} xs={12}>
              <Language>{t("Contact")}</Language>
              <Large onClick={() => window.location.href="mailto:team.roblian@gmail.com"}>{t("Tell us everything")}</Large>
              <Para>
                {t(`Do you have any question? Feel free to reach out.`)}
              </Para>
            </Col>
            <Col lg={8} md={8} sm={12} xs={12}>
              <Title>{t("Company")}</Title>
              <Large onClick={() => scrollTo("about")}>
                {t("About")}
              </Large>
              <Large onClick={() => scrollTo("team")}>
                {t("Team")}
              </Large>
              <Large onClick={() => scrollTo("games")}>
                {t("Games")}
              </Large>
              <Large onClick={() => scrollTo("mission")}>
                {t("Mission")}
              </Large>
            </Col>
            
            <Col lg={6} md={6} sm={12} xs={12}>
              <Label htmlFor="select-lang">{t("Language")}</Label>
              <LanguageSwitchContainer>
                <LanguageSwitch onClick={() => handleChange("en")}>
                  <SvgIcon
                    src="united-states.svg"
                    aria-label="homepage"
                    width="30px"
                    height="30px"
                  />
                </LanguageSwitch>
                <LanguageSwitch onClick={() => handleChange("kr")}>
                  <SvgIcon
                    src="korean.svg"
                    aria-label="homepage"
                    width="30px"
                    height="30px"
                  />
                </LanguageSwitch>
              </LanguageSwitchContainer>
            </Col>
          </Row>
        </Container>
      </FooterSection>
      <Extra>
        <Container border={true}>
          <Row
            justify="space-between"
            align="middle"
            style={{ paddingTop: "3rem" }}
          >
            <Col lg={10} md={10} sm={12} xs={12}>
              <Title>{t("Team Roblian. All rights reserved.")}</Title>
            </Col>

          </Row>
        </Container>
      </Extra>
    </>
  );
};

export default withTranslation()(Footer);
